
.taconews__header{
    text-align: center;
}

.taconews__container{
    height: auto;
    margin: 0 40px 40px 40px;
}

.taconews__grid{
    text-align: left;
    display: grid; 
    grid-template-columns: 1.2fr 0.9fr 0.9fr; 
    grid-template-rows: 1fr 1fr; 
    gap: 5% 3%; 
    grid-template-areas: 
    ". . ."
    ". . ."; 
    width: fit-content;
    height: 80vh;
    padding-inline: 20px;
}

.taconews__principal{
    background-color: var(--tacobell-purple);
    border-radius: 15px;
    grid-column: 1;
    grid-row: 1/3;
}

.taconews__principal .taconews__photo{
    padding: 20px;
    height: 50%;
    background-color: black;
    border-radius: 15px;
    background-position: center center;
    background-repeat: repeat-x;
    background-size: contain;
    -webkit-box-shadow: 0px -4px 15px #d2d2d2;
    box-shadow: 0px -4px 15px #d2d2d2;
}

.taconews__principal .taconews__info{
    padding: 20px;
}

.full *{
    color: white;
}

.taconews__date{
    font-size: 13.5px;
    font-weight: 100;
    text-transform: uppercase;
}

.taconews__title{
    overflow: hidden;
    display: -webkit-box;
    font-size: 19px;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-weight: 600;
    text-decoration: none;
    margin-top: 0.5em;
    margin-bottom: 1em;
    transition: 0.2s ease-in-out;
}

.taconews__title:hover{
    text-decoration: underline;
    transform: scale(1.05);
}

.taconews__description{
    font-size: 14px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}

.taconews__photo-normal{
    width: auto;
    height: 60%;
    padding: 0px;
    border-radius: 15px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.taconews__info-normal .taconews__date{
    margin-block: 10px;
    font-size: 13.5px;
    color: #333;
}

.taconews__info-normal .taconews__title{
    font-size: 19px;
    color: #1B193B;
}

.taconews__seemore{
    display: none;
}

@media screen and (max-width: 950px) {
    .taconews__description{
        -webkit-line-clamp: 3;
    }
}

@media screen and (max-width: 700px) {
    .taconews__grid{
        display: flex;
        width: fit-content;
        height: 40vh;
        padding-inline: 20px;
    }

    .taconews:nth-child(n+3){
        display: none;
    }

    .taconews__principal{
        background-color: transparent;
        border-radius: 15px;
    }
    
    .taconews__principal .taconews__photo{
        width: auto;
        height: 60%;
        padding: 0px;
        border-radius: 15px;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        -webkit-box-shadow: none;
        box-shadow: none;
    }
    
    .taconews__principal .taconews__info{
        padding: 0px;
    }

    .taconews__principal .taconews__date{
        margin-block: 10px;
    }
    
    .taconews__principal .taconews__description{
        display: none;
    }

    .full .taconews__date{
        color: #333;
    }

    .full .taconews__title{
        color: #1B193B;
    }

    .taconews__seemore{
        display: inline-block;
        font-size: 13.5px;
        text-decoration: none;
        margin-bottom: 10px;
    }

    .taconews__container .tacobell-h3{
        margin-bottom: 0;
    }
}